
let isInitialized = false;

function defineVanillaUI(config) {
	if (isInitialized) {
		console.warn('Vanilla UI has already been initialized. Initialization can only occur once.');
		return;
	}

	const { components } = config;

	if (!components || typeof components !== 'object' || Object.keys(components).length === 0) {
		console.error('No components specified in configuration or invalid components object.');
		return;
	}

	registerComponents(components);
	isInitialized = true;
	console.log('Vanilla UI initialized with provided components.');
}

function registerComponents(components) {
	for (const [name, component] of Object.entries(components)) {
		const hyphenatedName = name
			.replace(/([a-z])([A-Z])/g, '$1-$2')
			.toLowerCase();

		const prefixedName = hyphenatedName.includes('-')
			? hyphenatedName
			: `vanilla-${hyphenatedName}`;

		if (!customElements.get(prefixedName)) {
			customElements.define(prefixedName, component);
			console.log(`Registered: ${prefixedName}`);
		} else {
			console.warn(`Component ${prefixedName} is already registered and will not be overwritten.`);
		}
	}
}

// Export the defineVanillaUI function as the main entry point
// Also export the ShadcnDefault and ShadcnDefaultCSS components
export {
	defineVanillaUI,
};